<template>
  <div>
    <div class="wrapper" id="wrapper">
      <!-- ul需要滚动得元素 -->
      <div class="imgWrap">
        <swiper ref="mySwiper" :options="swiperOptions">
          <swiper-slide
            v-for="(item, index) in imgListL"
            :key="index"
            class="per"
          >
            <img :src="item" alt="" />
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
        <div class="title">
          <p>
            热映影片<router-link class="all" tag="span" to="/movie"
              >全部</router-link
            >
          </p>
        </div>
        <div class="content" id="content">
          <swiper ref="mySwiper" :options="swiperHotmovie">
            <swiper-slide v-for="item in list1" :key="item.id">
              <img :src="item.small" alt="" @click="toDetails(item.mId)" />
              <p>{{ item.title }}</p>
              <div class="button" @click="tocTicket">购票</div>
            </swiper-slide>
          </swiper>
        </div>
        <div class="title">
          <p>
            即将上映<router-link class="all" tag="span" to="/shangying"
              >全部</router-link
            >
          </p>
        </div>
        <div class="content p2">
          <swiper ref="mySwiper" :options="swiperComingsoon">
            <swiper-slide v-for="item in list2" :key="item.id">
              <img :src="item.small" alt="" @click="toDetails(item.mId)" />
              <p class="large">{{ item.title }}</p>
              <p>{{ item.show_date[0] }}</p>
            </swiper-slide>
          </swiper>
        </div>
        <div class="title">
          <p>
            精选预告<router-link class="all" tag="span" to="/movie"
              >全部</router-link
            >
          </p>
        </div>
        <div class="content p3">
          <swiper ref="mySwiper" :options="swiperPreview">
            <swiper-slide v-for="(item, index) in list3" :key="index">
              <video :src="item.video[0]" @click="topause(index)"></video>
              <div class="mask"><img :src="item.movie.large" alt="" /></div>
              <div class="play" @click="play(index)"></div>
              <p>
                《大鱼海棠》讲述了掌管海棠花生长的少女椿为报恩而努力复活人类男孩“鲲”的灵魂，
                在本是天神的湫帮助下与彼此纠缠的命运斗争的故事。影片于2016年7月8日在中国大陆上映。
                2017年12月3号，《大鱼海棠》获第15届布达佩斯国际动画电影节最佳
                ... >>>
              </p>
            </swiper-slide>
          </swiper>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import ban1 from "@/assets/images/home/banner1.png";
import ban2 from "@/assets/images/home/banner2.png";
import ban4 from "@/assets/images/home/banner4.png";
import ban5 from "@/assets/images/home/banner5.png";
import BScroll from "@better-scroll/core";

export default {
  name: "carrousel",
  data() {
    return {
      list1: [],
      list2: [],
      list3: [],
      num: 1,
      bScroll: "",
      imgListL: [ban2, ban1, ban4],
      swiperOptions: {
        slidesPerView: 1.3,
        // spaceBetween: 5,
        centeredSlides: true,
        loop: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
        },
      },
      swiperHotmovie: {
        slidesPerView: 3.08,
        spaceBetween: 1,
        centeredSlides: false,
        loop: true,
      },
      swiperComingsoon: {
        slidesPerView: 2.5,
        spaceBetween: 1,
        centeredSlides: false,
        loop: true,
      },
      swiperPreview: {
        slidesPerView: 1.1,
        spaceBetween: 5,
        centeredSlides: false,
        loop: false,
      },
    };
  },
  created() {},
  methods: {
    tocTicket() {
      this.$router.push("/cinema/cticket");
    },
    ale(val) {
      alert(val);
    },
    init1() {
      axios
        .get(`http://bufantec.com/api/douban/movie/in_theaters  `)
        .then((res) => {
          //console.log(res.data.data.list)
          //console.log(this)
          this.list1 = res.data.data.list;
          console.log(this.list1);
        });
    },
    init2() {
      axios
        .get(`http://bufantec.com/api/douban/movie/coming_soon `)
        .then((res) => {
          //console.log(res.data.data.list)
          //console.log(this)
          this.list2 = res.data.data.list;
          console.log(this.list2);
        });
    },
    init3() {
      axios
        .get(`http://bufantec.com/api/douban/movie/selectPreview`)
        .then((res) => {
          //console.log(res.data.data.list)
          //console.log(this)
          this.list3 = res.data.data;
          console.log(this.list3);
        });
    },
    toDetails(a) {
      console.log(a);
      this.$router.push({ name: "details", params: { id: a } });
    },
    play(index) {
      console.log(index);
      var video = document.getElementsByTagName("video")[index];
      var mask = document.getElementsByClassName("mask")[index];
      var play = document.getElementsByClassName("play")[index];
      mask.style.display = "none";
      play.style.display = "none";
      if (video.paused) {
        video.play();
      } else {
        video.pause();
      }
      var au = document.getElementsByTagName("video");
      for (var i = 0; i < au.length; i++) {
        au[i].addEventListener("playing", function() {
          var this1 = this;
          console.log(this1);
          for (var b = 0; b < au.length; b++) {
            var this2 = au[b];
            if (this1 !== this2) {
              this2.pause();
            }
          }
        });
      }
    },
    topause(index) {
      var play = document.getElementsByClassName("play")[index];
      if (event.target.played) {
        event.target.pause();
        play.style.display = "block";
      } else if (event.target.paused) {
        event.target.played();
      }
    },
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper;
    },
  },
  mounted() {
    //   console.log('Current Swiper instance object', this.swiper)

    setTimeout(() => {
      // nextTick 再dom更新完毕之后执行得回调函数
      this.$nextTick(function() {
        this.bScroll.refresh();
      });
    }, 1000);
    this.$store.state.currentPage = this.$router.currentRoute.fullPath;
    this.init1(), this.init2(), this.init3();
    this.swiper.slideTo(3, 1000, false);

    var content = document.getElementById("wrapper");
    this.bScroll = new BScroll(content, {
      click: true,
    });
    this.$nextTick(() => {
      this.bScroll.refresh();
    });
    setTimeout(() => {
      this.bScroll.refresh();
    }, 1000);
    setTimeout(() => {
      this.bScroll.refresh();
    }, 2000);
    setTimeout(() => {
      this.bScroll.refresh();
    }, 3000);
    setTimeout(() => {
      this.bScroll.refresh();
    }, 4000);
  },
};
</script>

<style lang="scss" scoped>
.imgWrap {
  margin-top: 1.326667rem;
}
#wrapper {
  height: calc(100vh - 5rem);
}
.per {
  width: 670px;
  height: 290px;
  transform: scale(0.8);
  img {
    width: 100%;
  }
}
.swiper-slide-active,
.swiper-slide-duplicate-active {
  transform: scale(1);
}

.title {
  margin: 0 auto;
  color: #dfdfdf;
  font-size: 36px;
  width: 664px;
}
.title p::before {
  content: "";
  display: inline-block;
  width: 8px;
  height: 34px;
  background-image: linear-gradient(90deg, #f26182 5%, #f1a064 100%);
  margin-right: 10px;
}
.all {
  float: right;
  margin-top: 8px;
  opacity: 0.78;
  font-size: 24px;
  height: 33px;
  vertical-align: middle;
}
.all::after {
  content: "";
  display: inline-block;
  width: 13px;
  height: 26px;
  background: url("../../assets/images/icons/arr-r.png") no-repeat center;
  background-size: contain;
  margin-left: 10px;
  vertical-align: middle;
}

.wrapper {
  height: 1650px;
  .content {
    margin-top: 16px;
    margin-left: 40px;
    margin-bottom: 56px;
    .swiper-slide {
      height: 340px;
      width: 180px;
      color: #dfdfdf;
      font-size: 24px;
      p {
        margin-top: 12px;
        width: 180px;
        height: 33px;
        line-height: 33px;
        text-align: center;
        //    word-break: break-all;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
      }
      img {
        width: 180px;
        height: 244px;
        border-radius: 15px;
      }
      .button {
        width: 100px;
        height: 44px;
        background-image: linear-gradient(150deg, #f25b86 0%, #f1ac5e 100%);
        box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.5);
        border-radius: 22px;
        text-shadow: 0 4px 8px rgba(49, 49, 49, 0.5);
        text-align: center;
        line-height: 44px;
        margin-top: 7px;
        margin-left: 40px;
      }
    }
  }
  .p2 {
    margin-bottom: 47px;
    .swiper-slide {
      height: 407px;
      width: 240px;
      p {
        width: 240px;
        height: 34px;
        margin-top: 0;
        opacity: 0.55;
        line-height: 34px;
        overflow: hidden;
      }
      .large {
        height: 40px;
        font-size: 28px;
        margin-top: 10px;
        opacity: 1;
      }
      img {
        width: 240px;
        height: 324px;
      }
    }
  }
  .p3 {
    margin-bottom: 48px;
    .swiper-slide {
      width: 590px;
      height: 410px;
      p {
        width: 590px;
        height: 74px;
        font-size: 26px;
        line-height: 37px;
        margin-top: 16px;
      }
      video {
        width: 590px;
        height: 320px;
        object-fit: cover;
      }
      .mask {
        display: block;
        width: 590px;
        height: 320px;
        position: absolute;
        top: 0;
        img {
          width: 590px;
          height: 320px;
          object-fit: cover;
        }
      }
      .play {
        width: 86px;
        height: 86px;
        background: url("../../assets/images/icons/play.png");
        background-size: contain;
        transform: translateY(128px);
        opacity: 0.7;
        position: absolute;
        top: 0;
        left: 258px;
      }
    }
    .swiper-slide .img.bg2 {
      //    background:url('../../assets/images/home/banner4.png') no-repeat center;
      background-size: 100%;
    }
  }
}
</style>
